body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/*
.rgba-primary-0 { color: rgba(150,165, 55,1) }
.rgba-primary-1 { color: rgba(236,248,165,1) }
.rgba-primary-2 { color: rgba(192,207,103,1) }
.rgba-primary-3 { color: rgba(110,124, 21,1) }
.rgba-primary-4 { color: rgba( 71, 83,  0,1) }
*/

a {
  color: rgba(192,207,103,1);
}

a:visited {
  color: rgba(192,207,103,1);
}

img {
  max-width: 100%;
}

#App {
  display: grid;
  grid-template-columns: 1fr 1fr;
}

.wrapper {
  background-color: rgba(150,165, 55,1);
  width: 100%;
  height: 100vh;
}

.heading {
  text-align: center;
  color: rgba( 71, 83,  0,1);
}

#preview {
  background-color: rgba( 71, 83,  0,1);
  color: white;
  font-size: 1.5em;
  overflow-y: scroll;
  width: 90%;
  height: 83%;
  margin: auto;
  margin-top: 2.5%;
  padding-left: 2.5%;
  padding-right: 2.5%;
}


#markdown {
  width: 90%;
  height: 96.5%;
  margin: auto;
  margin-top: 2.5%;
}

#editor {
  background-color: rgba(236,248,165,1);
  color: black;
  width: 100%;
  height: 85%;
  resize: none;
  font-size: 1.5em;
  outline: none;
  padding-left: 2.5%;
  padding-right: 2.5%;
}
